<template>
  <div id="app">
    <toplink/>
	<Header v-show="$root.bNav"/>
	<router-view></router-view>
	<Footer v-show="$root.bFoot"/>
	<!-- <Loading v-show="$root.bLoading"></Loading> -->
  </div>
</template>

<script>
import toplink from './components/toplink.vue'
import Header from './layouts/header.vue'
import Footer from './layouts/footer.vue'
// import Loading from './components/loading/loading.vue'
// import banner from './components/banner/banner.vue'

export default {
  name: 'app',
  components: {
    toplink,Header,Footer
	// ,Loading
  },
  watch:{
	  $route:{
		  handler(to){
			  let path=to.path;
			  if(/home|column/.test(path)){
				this.$root.bNav=this.$root.bFoot=true;
			  }
			  if(/detail|login|reg/.test(path)){
			    this.$root.bNav=this.$root.bFoot=false;
			  }
			  if(/add|user/.test(path)){
			    this.$root.bNav=false;
			    this.$root.bFoot=true;
			  }
		  },
		  immediate:true
	  }
  }
}
</script>

