<template>
	<div class="uc-cell">
		<div @click="toDetail">
			<h2><span v-show="index !=null">{{index+1}}.</span>{{data.title}}</h2>
			<p v-show="$route.path=='/home'">地址：{{data.address}}</p>
			<p v-show="$route.path=='/column'">类别：{{data.category_name}}</p>
			<img src="" alt="">
		</div>
	</div>
</template>

<script>
	export default{
		name:'uc-cell',
		props:{
			data:{
				type:Object,
				require:true
			},
			index:{
			  type:Number,
			  default: null
			},
			collectionName:{
			  type:String,
			  default:null
			}
		},
		methods:{
			toDetail(){
				console.log(this.data)
				if(this.collectionName){
					this.$router.push(`/detail/${this.data.id}?collectionName={this.collectionName}`)
				}
			}
		}
	}
</script>

<style scoped>
	.uc-cell{position:relative;top:0.1rem;color:#494d4d; margin:0 auto;clear: both;padding:0.2rem 0.4rem;border-bottom:1px dashed #ccc;}
	.uc-cell:active{
	  background: #ccc;
	}
	.uc-cell h2{max-height:0.9rem; font-size:0.33rem; overflow:hidden;}
	.uc-cell p{max-height:1.8rem; margin-top:0.05rem;overflow:hidden;}
