<template>
	<div class="detail" v-if="detail.title">
		<uc-nav/>
		<div class="content">
			<div class="header clear">
				<img :src="detail.image_hlarge" alt="" />
			</div>
			<div class="cont">
				<h3>{{detail.title }}</h3>
				<p class="author">作者：{{detail.owner.name}}<a><img src="../assets/img/like.png" alt="" /></a></p>
				<!-- <div>{{3 | fillzero}}</div> -->
				<div class="address">
					<p>地址：{{detail.address}}</p>
				</div>
				<p class="text-box" v-html="detail.content"  style="word-break:break-all;"></p>
			</div>
		</div>
		<uc-toolbar/>
	</div>
	<frame-screen v-else />
</template>

<script>
	import UcNav from '../components/uc-nav/uc-nav.vue'
	import UcToolbar from '../components/uc-toolbar/uc-toolbar.vue'
	import FrameScreen from '../components/frame-screen/frame-screen.vue'
	import axios from '../plugins/axios.js'
	export default {
		name: 'detail',
		data() {
			return {
				detail: []
			}
		},
		components:{
			UcNav,
			UcToolbar,
			FrameScreen
		},
		mounted() {
			axios({
				url: "/home/list",
				method: "get"
			}).then((ok) => {
				let id = this.$route.params.id;
				// console.log(id,this.$route.params.id)
				let data = ok.data.shouye.find(function(obj) {
					return obj.id == id
				})
				// console.log(data)
				this.detail = data
			})
		}
	}
</script>

<style scoped>
	.content{max-width:6.4rem; margin:0 auto; margin-top:0.6rem; background:#f2f4f5; padding-bottom:0.85rem;}
	.content .header{ padding:0.39rem 0.28rem 0.15rem 0.28rem; height:auto; background: none;}
	/* .header h2{ float:left; margin-right:0.18rem;} */
	.header img{width: 100%;}
	.content .cont{ padding:0 0.38rem; color:#494d4d;}
	.cont h3{ font-size:0.4rem; line-height:0.6rem; padding-bottom:0.25rem; border-bottom:1px solid #494d4d;}
	.cont .author{ float:left; margin-top:0.18rem; font-size:0.3rem;}
	.cont .address{float:right; height:0.24rem; line-height:0.24rem; margin:0.26rem 0; }
	.cont img{height: 0.3rem;}
	/* .author a{display: inline-block;width: 0.24rem;background: url(../assets/img/like.png);} */
	/* .cont a:hover{background: url(../assets/img/liked.png);} */
